<!-- src/views/PostDetails.vue -->
<template>
    <div class="container">
      <h2>{{ post.title }}</h2>
      <p><strong>Description:</strong> {{ post.description }}</p>
      <p v-if="post.price"><strong>Price:</strong> {{ post.price }}</p>
      <img v-if="post.image" :src="post.image" alt="Post Image" />
      <p><strong>Published by:</strong> {{ post.publisher }}</p>
      
      <div v-if="post.subtypes && post.subtypes.length">
        <h3>Subtypes</h3>
        <ul>
          <li v-for="(subtype, index) in post.subtypes" :key="index">
            <strong>{{ subtype.title }}:</strong> {{ subtype.description }}
          </li>
        </ul>
      </div>
      
      <p v-if="post.startDate"><strong>Start Date:</strong> {{ post.startDate }}</p>
      <p v-if="post.endDate"><strong>End Date:</strong> {{ post.endDate }}</p>
  
      <!-- Edit and Delete Buttons (only if the current user is the post owner) -->
      <div v-if="isOwner">
        <button @click="editPost">Edit</button>
        <button @click="deletePost">Delete</button>
      </div>
  
      <button @click="$router.push('/dashboard')">Back to Dashboard</button>
    </div>
  </template>
  
  <script>
  import { db, auth } from "@/firebase/firebaseConfig";
  import { doc, getDoc, deleteDoc, updateDoc } from "firebase/firestore";
  
  export default {
    data() {
      return {
        post: {},
        isOwner: false
      };
    },
    async created() {
      const postId = this.$route.params.id;
      const postDoc = await getDoc(doc(db, "entries", postId));
      if (postDoc.exists()) {
        this.post = postDoc.data();
        const user = auth.currentUser;
        this.isOwner = user && user.uid === this.post.userId; // Check if current user is the owner
      } else {
        console.error("Post not found");
      }
    },
    methods: {
      async deletePost() {
        const postId = this.$route.params.id;
        try {
          await deleteDoc(doc(db, "entries", postId));
          this.$router.push("/dashboard"); // Redirect to dashboard after deletion
        } catch (error) {
          console.error("Error deleting post:", error.message);
        }
      },
      editPost() {
        // Set form fields to the post data (assuming you have a separate page or modal for editing)
        this.$router.push({ name: "EditPost", params: { id: this.$route.params.id } });
      }
    }
  };
  </script>
  
  <style>
  /* Styling for the edit and delete buttons */
  button {
    background-color: #2575fc;
    color: white;
    padding: 10px 20px;
    border: none;
    border-radius: 5px;
    cursor: pointer;
    margin: 5px;
  }
  
  button:hover {
    background-color: #0057e7;
  }
  
  button.delete {
    background-color: #ff4d4d;
  }
  
  button.delete:hover {
    background-color: #ff3333;
  }
  </style>
  