<!-- src/components/Navbar.vue -->
<template>
    <nav class="navbar">
      <div class="nav-links">
        <router-link to="/">Home</router-link>
        <router-link v-if="!isAuthenticated" to="/login">Login</router-link>
        <router-link v-if="!isAuthenticated" to="/signup">Signup</router-link>
        <button v-if="isAuthenticated" @click="logoutUser" class="logout-button">Logout</button>
      </div>
    </nav>
  </template>
  
  <script>
  import { getAuth, onAuthStateChanged, signOut } from "firebase/auth";
  
  export default {
    name: "Navbar",
    data() {
      return {
        isAuthenticated: false
      };
    },
    created() {
      const auth = getAuth();
      onAuthStateChanged(auth, (user) => {
        this.isAuthenticated = !!user;
      });
    },
    methods: {
      async logoutUser() {
        const auth = getAuth();
        await signOut(auth);
        this.$router.push("/"); // Redirect to home after logout
      }
    }
  };
  </script>
  
  <style>
  .navbar {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    background-color: #2575fc;
    color: white;
  }
  
  .nav-links a,
  .nav-links button {
    margin: 0 10px;
    color: white;
    text-decoration: none;
    font-size: 1rem;
    border: none;
    background: none;
    cursor: pointer;
  }
  
  .nav-links button.logout-button {
    background-color: #ff4d4d;
    padding: 8px 16px;
    border-radius: 5px;
    color: white;
    font-weight: bold;
  }
  
  .nav-links button.logout-button:hover {
    background-color: #ff3333;
  }
  </style>
  