<template>
    <div class="container">
      <h2>Dashboard</h2>
      <button @click="$router.push('/post-form')" class="create-button">Create New Entry</button>
  
      <div v-if="posts.length" class="posts-container">
        <h3>All Posts</h3>
        <div v-for="post in posts" :key="post.id" class="post-card" @click="$router.push({ name: 'PostDetails', params: { id: post.id } })">
          <h4>{{ post.title }}</h4>
          <p>{{ post.description }}</p>
          <p>Published by: {{ post.publisher }}</p>
          <img v-if="post.image" :src="post.image" alt="Image" />
        </div>
      </div>
    </div>
  </template>
  
  <script>
  import { db } from "@/firebase/firebaseConfig";
  import { collection, getDocs } from "firebase/firestore";
  
  export default {
    data() {
      return {
        posts: []
      };
    },
    methods: {
      async fetchPosts() {
        const querySnapshot = await getDocs(collection(db, "entries"));
        this.posts = querySnapshot.docs.map(doc => ({ id: doc.id, ...doc.data() }));
      }
    },
    async mounted() {
      await this.fetchPosts();
    }
  };
  </script>
  
  <style>
  .posts-container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
    gap: 20px;
  }
  
  .post-card {
    padding: 15px;
    border: 1px solid #ddd;
    border-radius: 8px;
    background-color: #f9f9f9;
    cursor: pointer;
    transition: box-shadow 0.2s;
  }
  
  .post-card:hover {
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
  }
  
  .post-card img {
    max-width: 100%;
    border-radius: 5px;
    margin-top: 10px;
  }
  </style>
  